import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    profile: {},
  },
  mutations: {
    SET_PROFILE(state, payload) {
      state.profile = payload;
    },
  },
  actions: {
    fetchProfile({ commit }) {
      const url = "https://peaceful-shore-13462.herokuapp.com/api/profile";
      return axios
        .get(url, {
          params: {
            user: "emir",
          },
        })
        .then((res) => {
          commit("SET_PROFILE", res.data);
        });
    },
  },
  modules: {},
});
