<template>
  <div class="home">
    <v-app-bar absolute :color="appBarColor" dark>
      <ul class="d-flex nav__list">
        <li
          v-for="{ title, id, active } in navTabs"
          :key="id"
          class="nav__item"
          :class="{ active: active, 'mr-5': $vuetify.breakpoint.mdAndUp }"
          @click="goToSection(id)"
        >
          <button class="px-2" text>{{ title }}</button>
        </li>
      </ul>
    </v-app-bar>
    <Hero
      ref="hero"
      id="hero"
      @scrollDown="goToSection('#about')"
      :name="getProfile && getProfile.hero && getProfile.hero.name"
    />
    <About
      ref="about"
      id="about"
      :aboutMe="getProfile && getProfile.about && getProfile.about.aboutMe"
      :photoUrl="getProfile && getProfile.about && getProfile.about.photoURL"
      :contactDetails="
        getProfile && getProfile.about && getProfile.about.contactDetails
      "
    />
    <Stack ref="stack" id="stack" />
    <Works ref="works" id="works" />
    <Contact ref="contact" id="contact" />
    <Footer @scrollUp="goToSection('#hero')" />
  </div>
</template>

<script>
// @ is an alias to /src
import { mapState, mapActions } from "vuex";
import Hero from "@/views/sections/Hero";
import About from "@/views/sections/About";
import Stack from "@/views/sections/Stack";
import Works from "@/views/sections/Works";
import Contact from "@/views/sections/Contact.vue";
import Footer from "@/views/sections/Footer.vue";

export default {
  name: "Home",
  components: {
    Hero,
    About,
    Stack,
    Works,
    Contact,
    Footer,
  },
  data() {
    return {
      appBarColor: "rgba(0, 0, 0, 0.5)",
      navTabs: [
        {
          title: "Hero",
          id: "#hero",
          active: false,
        },
        {
          title: "About",
          id: "#about",
          active: false,
        },
        {
          title: "Stack",
          id: "#stack",
          active: false,
        },
        {
          title: "Works",
          id: "#works",
          active: false,
        },
        {
          title: "Contact",
          id: "#contact",
          active: false,
        },
      ],
      currentSectionOffset: 0,
      heroSectionOffset: 0,
      aboutSectionOffset: 0,
      stackSectionOffset: 0,
      worksSectionOffset: 0,
      contactSectionOffset: 0,
    };
  },
  computed: {
    ...mapState(["profile"]),
    getProfile() {
      return this.profile && this.profile;
    },
  },
  watch: {
    currentSectionOffset: {
      immediate: true,
      handler(val) {
        this.$nextTick(() => {
          if (this.aboutSectionOffset <= val) {
            this.appBarColor = "black";
          } else {
            this.appBarColor = "rgba(0, 0, 0, 0.5)";
            this.resetActive("hero");
          }
        });
      },
    },
  },
  methods: {
    ...mapActions(["fetchProfile"]),
    async goToSection(target) {
      const container = document.querySelector(".scrollable-content");
      const options = {
        duration: 500,
        offset: 0,
        easing: "easeInOutCubic",
        container,
      };
      this.currentSectionOffset = await this.$vuetify.goTo(target, options);
    },
    // scrollListener(e) {
    //   const scrollTop = e.target.scrollTop;
    //   const appBarHeight = 48;
    //   if (scrollTop < this.aboutSectionOffset - appBarHeight) {
    //     this.resetActive("hero");
    //   } else if (scrollTop > this.contactSectionOffset - appBarHeight) {
    //     this.resetActive("contact");
    //   } else if (scrollTop > this.worksSectionOffset - appBarHeight) {
    //     this.resetActive("works");
    //   } else if (scrollTop > this.stackSectionOffset - appBarHeight) {
    //     this.resetActive("stack");
    //   } else if (scrollTop > this.aboutSectionOffset - appBarHeight) {
    //     this.resetActive("about");
    //   }
    // },
    resetActive(id) {
      this.appBarColor = id === "hero" ? "rgba(0, 0, 0, 0.5)" : "black";
      this.navTabs.map((tab) => {
        tab.active = false;
        if (tab.id === `#${id}`) {
          tab.active = true;
        }
      });
    },
  },
  created() {
    this.fetchProfile();
  },
  mounted() {
    // const container = document.querySelector(".scrollable-content");
    // container.addEventListener("scroll", this.scrollListener);
    // this.heroSectionOffset = this.$refs.hero.$el.offsetTop;
    // this.aboutSectionOffset = this.$refs.about.$el.offsetTop;
    // this.stackSectionOffset = this.$refs.stack.$el.offsetTop;
    // this.worksSectionOffset = this.$refs.works.$el.offsetTop;
    // this.contactSectionOffset = this.$refs.contact.$el.offsetTop;
  },
  destroyed() {
    // const container = document.querySelector(".scrollable-content");
    // container.removeEventListener("scroll", this.scrollListener);
  },
};
</script>

