.v-toolbar,
.v-toolbar__content {
  height: fit-content !important;
}
.v-toolbar__content {
  padding: 12px !important;
}
.nav {
  &__list {
    list-style: none;
    padding: 0 !important;
    margin: 0 auto;
  }
  &__item {
    transition: 0.4s;
    cursor: pointer;
    button {
      font-family: Libre Baskerville;
      text-transform: uppercase;
      font-weight: 700;
      color: #fff;
      outline: none;
      cursor: pointer;
      font-family: inherit;
      background: transparent;
      border: 0;
      transition: 0.4s;
    }
    &.active {
      button {
        color: #f06000 !important;
      }
    }
  }
}
